.BttonsWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.container {
  min-width: 375px;
  max-width: 480px;
  box-sizing: border-box;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    max-width: 1000px;
    padding: 30px;
  }

  @media screen and (min-width: 1440px) {
     min-width: 1440px;
    padding: 30px;
    // width: 1440px;
  }
}
